html,body,#root{
    height: 100%;
    padding: 0;
    margin: 0;
}
.mainContainer{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    display:flex;
    flex-wrap: wrap;
    background: purple;
    align-items: stretch;
}
.topBar{
    width: 100%;
    height: 10%;
    background: pink;
}

.leftBar{
    width: 15%;
    height: 80%;
    background: orange;
}

.midBar{
    width: 70%;
    height: 80%;
    background: red;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.rightBar{
    width: 15%;
    height: 80%;
    background: turquoise;
}

.bottomBar{
    width: 100%;
    height: 10%;
    background: maroon;
}